import { Component, OnInit } from '@angular/core';
import { GalleryService } from '../gallery.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
  keyWord : string;
  pagePhotos:any;
  currentPage: number=1;
  size:number=5;
  totalPages:number=0;
  motCle:string="";
  pages:Array<number>=[];
  constructor(private galleryService : GalleryService) { }

  ngOnInit() {
  }

  onSearch(dataForm) {
    console.log(dataForm.keyWord);  
    this.galleryService.search(dataForm.keyWord,this.size, this.currentPage).subscribe(data=>{
      //console.log(JSON.stringify(data));  
      this.pagePhotos=data;
      this.totalPages = data['totalHits']/this.size;
      console.log(this.totalPages);
      if (this.totalPages % 2 !=0) this.totalPages = Math.round(this.totalPages);
      console.log(this.totalPages);
      this.pages = new Array(this.totalPages);
      
    }, err=>{
      console.log(err);
    });
  }

  goToPage(i) {
    this.currentPage = i+1;
    console.log(this.motCle);
    this.onSearch ({keyWord:this.motCle});
  }
}



